export default {
	data() {
		return {
			show: false,
			username: '',
			password: '',
			code: '',
			msg:"no",
			num:1,
			imgs:"",
			baseUrl:""
		};
	},
	onShow() {
		uni.setNavigationBarTitle({
			title: this.$t('index.denglu')
		})	
		// console.log('now:' + this.websiteUrl); 
		this.baseUrl = this.websiteUrl;
		this.imgs = this.baseUrl + "/getActivationCode?id=1"
		// console.log(this.baseUrl);
	
	},
	computed: {
		i18n() {
			return this.$t('index');
		}
	},
	methods: {
		changeLangu() {
			console.log('语言切换');
			const system_info = uni.getStorageSync('system_info');
			system_info.language === 'en' ? (system_info.language = this._i18n.locale = 'zh_CN') : (system_info.language = this._i18n.locale = 'en');
			uni.setStorageSync('system_info', system_info);
			uni.reLaunch({
				url: 'index'
			});
			if(system_info.language === 'en'){
				uni.setNavigationBarTitle({
					title: this.$t('index.denglu')
				})
			
			}else {
				uni.setNavigationBarTitle({
					title: this.$t('index.denglu')
				})
			
			}
		},
		messagesearch: function(event) {
			// 这里动态获取VALUE 值
			// console.log(event.target.value)
			this.username = event.target.value;
		},
		passwordsearch: function(event) {
			// 这里动态获取VALUE 值
			// console.log(event.target.value)
			this.password = event.target.value;
		},
		codesearch: function(event) {
			// 这里动态获取VALUE 值
			// console.log(event.target.value)
			this.code = event.target.value;
		},
		yan_ma(){
			console.log(this.baseUrl)
			++this.num
			this.imgs = this.baseUrl + "/getActivationCode?id=" + this.num;
		},
		change() {
			this.show = !this.show;
			console.log(this.show);
				
			if(this.show == true){
				this.msg = "yes"
			}else {
				this.msg = "no"
			}
		},
		// codeClick(){
		// 	let username = this.username;
		// 	let password = this.password;
		// 	if (username == '' || username == null || username == undefined) {
		// 		uni.showToast({
		// 			title: '请输入账号',
		// 			icon: 'none',
		// 			duration: 800
		// 		});
		// 		return false;
		// 	} else if (password == '' || password == null || password == undefined) {
		// 		uni.showToast({
		// 			title: '请输入密码',
		// 			icon: 'none',
		// 			duration: 800
		// 		});
		// 		return false;
		// 	}
		// },
		// 登陆
		loginsxsss() {
			// console.log(this.$refs['bangding'].$attrs.have);
			console.log(this.username);
			let username = this.username;
			let password = this.password;
			let code = this.code;
			console.log(this.msg);
			let agree = this.msg;
			if (username == '' || username == null || username == undefined) {
				uni.showToast({
					title: '请输入账号',
					icon: 'none',
					duration: 800
				});
				return false;
			} else if (password == '' || password == null || password == undefined) {
				uni.showToast({
					title: '请输入密码',
					icon: 'none',
					duration: 800
				});
				return false;
			} else if (code == '' || code == null || code == undefined) {
				uni.showToast({
					title: '请输入验证码',
					icon: 'none',
					duration: 800
				});
				return false;
			}else if(agree == "no") {
				uni.showToast({
				    title: "您还没有同意隐私和协议",
					icon:"none",
				    duration: 800
				});
				return false;
			}else if (agree == "yes"){
				
				uni.request({
					url: this.baseUrl + '/api/login',
					method: 'post',
					data: {
						account:username,
						password:password,
						code:code
					},
header:{
						'content-type':"application/x-www-form-urlencoded"
					},					
					success: res => {
						console.log(res.data);
						
						// uni.showToast({
						//     title: res.data.msg,
						// 	icon:"none",
						//     duration: 500
						// });
						if(res.data.code == "200") {
							// console.log(res.data.data.token);
							uni.setStorage({
							    key: 'storage_token',
							    data: res.data.data.token,
							    success: function () {
							        console.log('success');
							    }
							});
							
							uni.setStorage({
							    key: 'num',
							    data: "1",
							    success: function () {
							        console.log('success');
							    }
							});
							
							uni.showToast({
							    title: res.data.msg,
								icon:"none",
							    duration: 500
							});
							setTimeout(function(){
								uni.switchTab({
								         url: '../index/index'
								});
							},700)
						}else {
							uni.showToast({
							    title: res.data.msg,
								icon:"none",
							    duration: 500
							});
						}

					},
					fail: () => {},
					complete: () => {}
				});
				
				// 上一个页面
				  // uni.navigateBack();
					// 首页
				// uni.switchTab({
				//          url: '../index/index'
				// });
			}
		},
		zhuce(){
			uni.navigateTo({
			         url: '../register/register'
			});
		},
		forget(){
			uni.navigateTo({
			         url: '../forget/forget'
			});
		},
		yinsi() {
			uni.navigateTo({
				url: '../yinsizhengce/yinsizhengce'
			});
		},
		xieyi() {
			uni.navigateTo({
				url: '../yonghuxieyi/yonghuxieyi'
			});
		}
	}
